const aboutUsTitle = 'professor marcelo yogui'
const aboutUsText = ['Jag heter Marcelo Galvao Santiago, men jag är mer känd under namnet Marcelo Yogui. Jag har tränat brasiliansk jiu-jitsu i mer än 30 år och började när jag var 15 år gammal.', 'Min träning började under professor Rillion Gracie, och efter att ha lärt mig grunderna där (vilka jag fortfarande använder och lär ut idag), så började jag träna med professor Carlos Gracie jr. från Gracie Barra Team som är ordförande för det brasilianska och internationella förbundet. Jag måste också tacka Renzo Gracie, bröderna Machado och många andra träningspartners jag har haft nöjet att träna med.', 'Efter en framgångsrik karriär träffade jag min vän Peter Zethraeus i Rio de Janeiro redan 1996. Han var den första som gav mig chansen att komma till Sverige och presentera min sport. Jag var en av pionjärerna som tog brasiliansk jiu-jitsu till skandinavien. När jag besökte Sverige senare samma år var brasiliansk jiu-jitsu mycket sällsynt och endast ett fåtal klubbar var aktiva. Vi var ett gäng hängivna personer som älskade sporten och lyckades utöka våra rötter i hela skandinavien och få fram till ett bra lag som lyckades vinna stora tävlingar runt om i Europa och även i Brasilien.', 'Under min resa inom BJJ har jag själv lyckats vinna några bra titlar, som panamerikansk mästare, brasiliansk mästare, europamästare och även några andra lokala tävlingar. Förutom att driva min egen akademi i Rio och undervisa i skandinavien har jag undervisat på många ställen över hela Europa, Nord- och Sydamerika och även i Hong Kong och på vissa platser i USA. Nu är jag baserad i Göteborg i Sverige och reser fortfarande runt och undervisar och har även ett bra samarbete med Shootfighting Association.', 'Jag är stolt över alla mina elever och alla människor jag har arbetat med och kunnat hjälpa genom åren. Jag vill tacka Babak Morteza Aghavali från Superior wear och även min gode vän och partner för allt stöd och all vänskap han har gett mig.', 'Tack.']

const aboutUsTextEng = ['My name is Marcelo Galvao Santiago, but I am more known by the name Marcelo Yogui. I have been practising Brazilian Jiu-Jitsu for more than 30 years and started when I was 15 years old.', 'My training started under Professor Rillion Gracie but after some months after get my good beginning and all the basics with Professor Rillion ,what i´m using and also give to my students ,I started to train with the Professor Carlos Gracie jr. from Gracie Barra Team, who is the president of the Brazilian confederation and the international federation,and also have to thanks Renzo Gracie,the Machado brother´s and many others trainning partners i had the pleasure to train with.', 'After a successful career I met my friend Peter Zethraeus in Rio de Janeiro back in 1996. He was the first person who gave me the chance to come to Sweden and introduce my sport. I was one of the pioneers who brought Brazilian Jiu-Jitsu to Scandinavia. When I visited Sweden later the same year Brazilian Jiu-Jitsu was very rare and only a few clubs were active. We and some other good people who loved the sport succeeded with expanding its roots throughout Scandinavia and to come up with a good team that managed to win big competitions all around Europe and even in Brazil.', 'During my fighting history I have managed to come up with some good titles myself, like Pan American Champion, Brazilian Champion, European Champion and also some other local competitions. Besides running my own academy in Rio and instructing in Scandinavia I have been teaching in many places all over Europe, North and South America and also in Hong Kong and some places in USA Now I´m based in Göteborg in Sweden and still travel around teaching and also have a good partnership with The shootfighting association.', 'I am proud of all my students and all the people I have worked with and been able to help through the years. I want to say thank you to Babak Morteza Aghavali from Superior wear and also my good friend and partner for all the support and friendship he has given me.', 'Thank you.']

const aboutBJJTitle = 'brazilian jiu-jitsu'
const aboutBJJText = ['Brasiliansk jiu-jitsu (BJJ) härstammar från japansk jiu-jitsu och judo, men har i årtionden vidareutvecklats av familjen Gracie från Brasilien. BJJ är en grapplingstil som innebär att du inte använder slag eller sparkar, utan istället fokuserar på lås och positioner för att oskadliggöra din motståndare. Grundidén är att få ner din motståndare på marken, kontrollera honom / henne att stanna där och med hjälp av lås och strypningar tvinga dem att ge upp. BJJ brukar lyftas fram som kampsporten där mindre motståndare har störst möjlighet att övermanna större och starkare. Anledningen till detta är att tekniken alltid kommer före fysiska egenskaper som storlek och styrka. Genom att fokusera på teknik och koncentrera sig på att hålla kampen främst på marken ger personen med den mindre kroppsstorleken en fördel. Detta är magin som gör BJJ till den kampsport som passar alla pojkar och flickor, små och stora, unga och gamla!']
const aboutBJJTextEng = ['Brazilian jiu-jitsu (Bjj) is derived from Japanese jiu-jitsu and judo, but has for decades been further developed by the Gracie family from Brazil. BJJ is a grappling-style which means that you do not use punches or kicks, but instead focus on joint locks and strangulations that incapacitate your opponent. The basic idea is to bring down your opponent to the ground, control him/her to stay there and with the help of joint locks and strangulations compel them to give up. Bjj is usually emphasized as the martial art where smaller opponents have the greatest opportunity to overpower larger and stronger ones. The reason for this is that technique always comes before physical characteristics such as size and strength. By focusing on technique and concentrating on the keeping the fight mainly on the ground does give the person with the smaller body size an advantage. This is the magic that makes Bjj the martial art that is suitable for all boys and girls, small and large, young and old!']
const aboutSWTitle = 'submission wrestling'
const aboutSWText = ['Submission wrestling (även känd som Grappling och No-Gi Jiu Jitsu) är en modern, hybrid kampsport med kast från Judo, take-downs från brottning, svep och chokes från brasiliansk jiu-jitsu, lås från Shooto och Shootfighting etc. Målet i sporten är att tvinga din motståndare att ge upp utan att använda sparkar eller slag. Grepp, kast, strypningar och lås är tillåtna. Du vinner genom att moståndaren ger upp (avslut) eller poäng. Submission Wrestling tränas över hela världen och det finns tävlingar för alla nivåer. Federation of Associated Wrestling Styles (FILA) organiserar Grappling Championships årligen. FILE är den största organisationen inklusive olympiska evenemang.']
const aboutSWTextEng = ['Submission wrestling (also known as the Grappling and No-Gi Jiu Jitsu) is a modern, hybrid martial art with throws from Judo, take-downs from wrestling, sweeps and chokes from Brazilian Jiu Jitsu, locks from Shooto and Shootfighting etc. The basics of the sport is forcing your opponent to give up without using kicks or blows. Grip, throws, chokes and locks are allowed. You win by submission (finishes) or points. Submission Wrestling is practiced worldwide and there are competitions for all levels. Federation of Associated Wrestling Styles (FILA) organizes Grappling Championships annually. FILE is the largest organization including Olympic events.']

